import React from "react";
import { Link } from "react-router-dom";

function Camcheck(){
    return(
        <div className="camcheck-box">
            <Link to="/CheckCamMic">
                <button type="button" className="btn btn-primary btn-sm" style={{width:"50%", height:"30%"}}>
                      CAM 
                </button>
            </Link>    
        </div>
    )


}

export default Camcheck;

